import * as yup from "yup";

export const validationSchemaForContact = yup.object().shape({
  name: yup
    .string()
    .required("  ")
    .min(3, "      3 "),

  email: yup
    .string()
    .required("   ")
    .email("    "),

  message: yup
    .string()
    .required("  ")
    .min(10, "     10   ")
    .max(500, "    500 "), //     
});
